const XP_GAIN = "XP_GAIN";
const LEVEL_UP = "LEVEL_UP";
const PICK_ADENA_UP = "PICK_ADENA_UP";
const INCREASE_DMG = "INCREASE_DMG";
const SET_CHARACTER_LVL = "SET_CHARACTER_LVL";
const SET_CHARACTER_ADENA = "SET_CHARACTER_ADENA";
const SET_CHARACTER_XP = "SET_CHARACTER_XP";

let initialState = {
  characterLVL: 1,
  experienceForLvl: {
    level_1: 68,
    level_2: 296,
    level_3: 805,
    level_4: 1716,
    level_5: 3154,
    level_6: 5249,
    level_7: 8136,
    level_8: 11955,
    level_9: 16851,
    level_10: 22973,
    level_11: 30475,
    level_12: 39516,
    level_13: 50261,
    level_14: 62876,
    level_15: 77537,
  },
  characterHP: 100,
  characterDMG: 15,
  characterMP: 100,
  characterXP: 0,
  characterAdena: 0,
};

export const characterReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CHARACTER_LVL: {
      let stateCopy = { ...state };
      stateCopy.characterLVL = action.characterLVL;
      return stateCopy;
    }
    case SET_CHARACTER_XP: {
      let stateCopy = { ...state };
      stateCopy.characterXP = action.characterXP;
      return stateCopy;
    }
    case SET_CHARACTER_ADENA: {
      let stateCopy = { ...state };
      stateCopy.characterAdena = action.characterAdena;
      return stateCopy;
    }

    case XP_GAIN: {
      let stateCopy = { ...state };
      stateCopy.characterXP += action.xp;
      localStorage.removeItem("characterXP");
      localStorage.setItem("characterXP", stateCopy.characterXP);
      return stateCopy;
    }
    case LEVEL_UP: {
      let stateCopy = { ...state };
      stateCopy.characterXP = 0;
      stateCopy.characterLVL = state.characterLVL + 1;
      localStorage.removeItem("characterLVL");
      localStorage.setItem("characterLVL", stateCopy.characterLVL);
      return stateCopy;
    }

    case PICK_ADENA_UP: {
      let stateCopy = { ...state };
      stateCopy.characterAdena += action.adena;
      localStorage.removeItem("adena");
      localStorage.setItem("adena", stateCopy.characterAdena);
      return stateCopy;
    }

    case INCREASE_DMG: {
      let stateCopy = { ...state };
      stateCopy.characterDMG += 5;
      stateCopy.characterAdena -= 50;
      localStorage.removeItem("characterDMG");
      localStorage.removeItem("adena");
      localStorage.setItem("characterDMG", stateCopy.characterDMG);
      localStorage.setItem("adena", stateCopy.characterAdena);

      return stateCopy;
    }

    default:
      return state;
  }
};

export const setCharacterLVL = (characterLVL) => ({
  type: SET_CHARACTER_LVL,
  characterLVL: characterLVL,
});
export const setCharacterXP = (characterXP) => ({
  type: SET_CHARACTER_XP,
  characterXP: characterXP,
});
export const setCharacterAdena = (characterAdena) => ({
  type: SET_CHARACTER_ADENA,
  characterAdena: characterAdena,
});
export const xpGain = (gainedXP) => ({ type: XP_GAIN, xp: gainedXP });
export const levelUp = () => ({ type: LEVEL_UP });
export const pickAdenaUp = (adena) => ({ type: PICK_ADENA_UP, adena: adena });
export const increaseDMG = () => ({ type: INCREASE_DMG });
